body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Sport {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto;
  grid-template-areas: "s1 s3" "s2 s2";
  margin-top: 2rem; }
  .Sport__sect1 {
    grid-area: s1;
    margin: 1rem auto; }
    .Sport__sect1 > div {
      display: flex;
      flex-direction: row; }
  .Sport__sect2 {
    grid-area: s2;
    margin: 1rem auto; }
    .Sport__sect2 > div {
      display: flex;
      flex-direction: row; }
  .Sport__sect3 {
    grid-area: s3;
    margin: 1rem auto; }
    .Sport__sect3 > div {
      display: flex;
      flex-direction: row; }
  .Sport__sect4 {
    grid-area: s4;
    margin: 1rem auto; }
    .Sport__sect4 > div {
      display: flex;
      flex-direction: row; }
  .Sport__legend {
    list-style: none; }
    .Sport__legend > li {
      display: flex;
      flex-direction: row;
      align-items: center; }
    .Sport__legend svg {
      height: 1rem;
      margin-right: 0.5rem; }

.rv-xy-plot__inner {
  height: calc(100% * 5); }

.App {
  display: grid;
  grid-template-columns: 8% auto;
  grid-template-areas: "tabs sports"; }
  .App__tabs {
    display: flex;
    flex-direction: column;
    grid-area: tabs;
    justify-content: start;
    margin: 1rem; }
    .App__tabs > button {
      background: transparent;
      border: none;
      cursor: pointer;
      padding: 1.5rem; }
      .App__tabs > button > svg {
        cursor: pointer;
        width: 2rem; }
  .App__sport {
    grid-area: sports; }

